import './SignUp.css'
import headerImg from '../img/headerImg.png';
import { useNavigate } from 'react-router-dom'

const SignUp = () => {
    const navigate= useNavigate();
    const joinFunc = ()=>{
        alert(' .')
        navigate(-1);
    }

    return (  
        <section className='SignUp'>
            <div className="infoTit">
                <img src={headerImg}></img>
                <h3></h3>
            </div>
            <div className='w1500'>
                <h3 className='titBox2'></h3>
                <div className='SignUpMain'>
                    <div className='inputBox'>
                        <div className='inputDiv'>
                            <label></label>
                            <input className='input' type='text'></input>
                        </div>
                        <div className='inputDiv'>
                            <label></label>
                            <input className='input' type='text'></input>
                            <div className='idchk'>
                                <button className="button"></button>
                                <p className='redTxt p'>* ID  8 ~ 16 .</p>
                            </div>
                        </div>
                        <div className='inputDiv'>
                            <label></label>
                            <input className='input' type='password' placeholder='8~16 ,,'></input>
                        </div>
                        <div className='inputDiv'>
                            <label> </label>
                            <input className='input' type='password'></input>
                        </div>
                        <div className='birth '>
                            <div className='inputDiv'>
                                <label></label>
                                <input className='input' type="radio" name="y"></input><span></span>
                                <input className='input' type="radio" name="y"></input><span></span>
                            </div>
                            <div className='selectDiv'>
                                <select className='select'>
                                    <option selected>1999</option>
                                    <option>1997</option>
                                    <option>1989</option>
                                </select>
                                <select className='select'>
                                    <option>1</option>
                                    <option>2</option>
                                    <option>3</option>
                                    <option>4</option>
                                    <option>5</option>
                                    <option>6</option>
                                    <option>7</option>
                                    <option>8</option>
                                    <option>9</option>
                                    <option selected>10</option>
                                    <option>11</option>
                                    <option>12</option>
                                </select>
                                <select className='select'>
                                    <option selected>13</option>
                                </select>
                            </div>
                        </div>

                        <div className='telphone'>
                            <label></label>
                            <div>
                                <select className='select'>
                                    <option selected>010</option>
                                </select>
                                <input className='telInput input' type='text'></input>
                                <input className='telInput input' type='text'></input>
                            </div>
                            <button className="button"></button>
                        </div>
                        <div className='eMail'>
                            <label></label>
                            <div>
                                <input className='mailInput input' type='text'></input>
                                <div className='middle'>@</div>
                                <input className='mailInput input' type='text'></input>
                                <select className='select'>
                                    <option selected></option>
                                </select>
                            </div>
                            <button className='button'></button>
                        </div>
                        <div className='inputDiv'>
                            <label> </label>
                            <input className='input' type="checkbox"></input>
                            <p>&#91;&#93;   </p>
                        </div>
                        <div className='inputDiv'>
                            <label>/</label>
                            <input className='input' type="checkbox"></input>
                            <p>&#91;&#93;       </p>
                        </div>
                        <div className='inputDiv'>
                            <label>   </label> 
                            <input className='input' type="checkbox"></input>
                            <p>&#91;&#93;       </p>
                        </div>
                    </div>
                </div>
                <div className='SignUpBtn'>
                    <button className='button' onClick={joinFunc}></button>
                </div>
            </div>
        </section>
    );
}
 
export default SignUp;