import '../pageCss/section4_FAQ.css';  // CSS  .
import headerImg from '../img/headerImg.png';  //    .
import iconQ from '../img/Q.png';  //    .
import iconA from '../img/A.png';  //    .
import { useRef, useEffect, useState } from 'react';  // React  useRef, useEffect, useState .
import { Link } from 'react-router-dom';

const Section4_FAQ = () => {
    const [activeIndex, setActiveIndex] = useState(null);  //     
    const [isLiOn, setLiOn] = useState(0); //    0 
    const lis = useRef([]);  // DOM    ref

    const faqData = [
        {
            question: ' ?',
            answer: ',   .',
        },
        {
            question: '   ?',
            answer: '     .    .',
        },
        {
            question: '    ?',
            answer: '  , , ,  ,      .',
        },
        {
            question: '     ?',
            answer: '     ,        .',
        },
    ];

    const toggleAccordion = (index) => {
        if (activeIndex === index) {
            //     .
            setActiveIndex(null);
        } else {
            //   .
            setActiveIndex(index);
        }
    };

    const handleMouseOver = (index) => {
        setLiOn(index);
    };

    useEffect(() => { 
        //   ,    .


    
            lis.current.forEach((li, index) => {
                li.addEventListener('mouseover', () => handleMouseOver(index));
                li.addEventListener('mouseleave', () => handleMouseOver(null)); //    null 
            });
    
            return () => {
                window.onload = function(){
                //      .
                lis.current.forEach((li, index) => {
                    li.removeEventListener('mouseover', () => handleMouseOver(index));
                    li.removeEventListener('mouseleave', () => handleMouseOver(null));
                });
            }
            };
        
    }, []);

    return (
        <section className='section4_subPage'>
              <div className="infoTit">
                    <img src={headerImg}></img>
                    <h3>FAQ</h3>
                </div>
            <div className='w1500'>
                <div className='Section4_announcementBtns'>
                    <ul className='flex2'>
                        {/*        /  . */}
                        <Link to='/NoticeList'><li className={isLiOn === 0 ? 'on' : 'off'} ref={(li) => (lis.current[0] = li)}></li></Link>
                        <Link to='/NoticeWrite'><li className={isLiOn === 1 ? 'on' : 'off'} ref={(li) => (lis.current[1] = li)}></li></Link>
                        <Link to='/FAQ'><li className={isLiOn === 2 ? 'on' : 'off'} ref={(li) => (lis.current[2] = li)}>FAQ</li></Link>
                    </ul>
                </div>    
                <article className='faqArticle'>
                    <div className='faqTitle'></div>
                    {faqData.map((item, index) => (
                        <div key={index}>
                            <div
                                className={`accordion ${activeIndex === index ? 'active' : ''}`}
                                onClick={() => toggleAccordion(index)}
                            >
                                <div className='iconQ'><img src={iconQ} alt="Question Icon" /></div>
                                <p>{item.question}</p>
                            </div>
                            <div className={`panel ${activeIndex === index ? 'active' : ''}`}>
                                <div className='iconA'><img src={iconA} alt="Answer Icon" /></div>
                                <p>{item.answer}</p>
                            </div>
                        </div>
                    ))}
                </article>
            </div>
        </section>
    );
}

export default Section4_FAQ;