import './login.css';
import topImg from '../img/headerImg.png';
import { Link } from 'react-router-dom';

const Login = () => {
    return ( 
        <section className="login">
            <article className="infoTit">
                <img src={topImg}></img>
                <h3></h3>
            </article>
            <article className='w1500'>
                <h3 className='titBox'></h3>
                <div className='loginArea'>
                    <div>
                        
                    </div>
                    <div className='tabContent'>
                        <form action="" method="post">
                            <div className='formItem'>
                                <div><input type='text' name='id' id='id' placeholder=''/></div>
                                <div><input type='password' name='pwd' id='pwd' placeholder=''/></div>
                            </div>
                            <div className='chkBox'>
                                <div>
                                    <input type='checkbox' id='idchk'/>
                                    <label for='idchk'></label>
                                </div>
                                <div>
                                      |  
                                </div>
                            </div>
                            <div className='btnWrap'>
                                <div><input type='submit' value=''></input></div>
                                <div><Link to="/Join"><input type='button' value=''></input></Link></div>
                            </div>
                        </form>
                    </div>
                </div>
            </article>
        </section>
     );
}
 
export default Login;